<template>
  <div>
    <v-app-bar elevation="4" color="blue" dark fixed>
      <!-- <v-icon @click="drawer = true">menu</v-icon> -->
      <v-img
        src="../assets/e-unsplash-small.jpg"
        height="30"
        width="30"
        max-width="30"
      />
      <v-icon class="ml-2" @click="mapDialog = true">map</v-icon>
      <v-text-field
        class="mx-2"
        placeholder="請輸入關鍵字"
        v-model="search"
        outlined
        hide-details
        rounded
        dense
        color="white"
        filled
      >
        <template #append>
          <div @click.stop.prevent="toSearch()">
            <v-icon>search</v-icon>
          </div>
        </template>
      </v-text-field>
      <router-link
        to="/notifications-list"
        class="text-decoration-none d-block"
      >
        <v-icon>notifications</v-icon>
      </router-link>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" app disable-resize-watcher>
      <v-card outlined class="pa-2 d-flex" style="height: auto">
        <v-icon size="80">account_circle</v-icon>
        <div class="ml-2">
          <div>王小明</div>
          <div>xxxxxxxx</div>
          <v-btn outlined small class="mt-1" @click="dialog = true"
            >顯示員工卡片</v-btn
          >
        </div>
      </v-card>
      <v-list nav dense>
        <!-- <v-subheader>
          <router-link class="mx-1 text-decoration-none" to="/">
            回首頁
          </router-link>
        </v-subheader> -->
        <v-list-item-group active-class="blue--text text--accent-4">
          <v-list-item v-for="(item, i) in list" :key="i">
            <v-list-item-title
              @click="$store.commit('setListPageType', { title: item.text })"
            >
              <router-link
                class="mx-1 text-decoration-none"
                :to="item.to"
                style="display: block"
              >
                {{ item.text }}
              </router-link>
            </v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <v-dialog v-model="dialog" width="80%" content-class="overflow">
      <v-card class="pt-6">
        <v-card-text class="d-flex">
          <v-icon size="110" style="margin-left: -10px">person</v-icon>
          <div>
            <p>xxxxxxxx</p>
            <p>王小明</p>
            <p>oo課</p>
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="mapDialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-app-bar dark color="#2196f3">
          <!-- <v-icon @click="$router.go(-1)">chevron_left</v-icon> -->
          <router-link to="/home">
            <v-img
              src="../assets/e-unsplash-small.jpg"
              height="30"
              width="30"
              max-width="30"
              class="ml-2"
            />
          </router-link>
          <v-icon class="ml-2" @click="dialog = true">map</v-icon>
          <v-spacer></v-spacer>
          <span>{{ title || listPageType }}</span>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-icon dark text @click="setCities"> close </v-icon>
          </v-toolbar-items>
        </v-app-bar>
        <v-container fluid>
          <v-chip-group v-model="selectCities" column multiple>
            <v-chip
              filter
              outlined
              filter-icon="check"
              color="primary"
              v-for="(city, i) in cities"
              :key="i"
            >
              {{ city }}
            </v-chip>
          </v-chip-group>
        </v-container>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data: () => ({
    drawer: false,
    search: "",
    dialog: false,
    mapDialog: false,
    cities: [
      "臺北市",
      "新北市",
      "桃園市",
      "臺中市",
      "臺南市",
      "高雄市",
      "新竹市",
      "嘉義市",
      "新竹縣",
      "苗栗縣",
      "彰化縣",
      "南投縣",
      "雲林縣",
      "嘉義縣",
      "屏東縣",
      "宜蘭縣",
      "花蓮縣",
      "臺東縣",
      "澎湖縣",
      "金門縣",
      "連江縣",
      "基隆市",
    ],
    selectCities: [],
  }),

  mounted() {
    let cities = JSON.parse(localStorage.getItem("selectCities"));
    if (cities && cities.length) {
      this.selectCities = cities;
    }
  },

  computed: {
    ...mapState(["list"]),
  },

  methods: {
    setCities() {
      this.mapDialog = false;
      localStorage.setItem("selectCities", JSON.stringify(this.selectCities));
    },
  },
};
</script>

<style scoped>
.overflow {
  overflow: hidden !important;
}
</style>