import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import BottomNavigation from '@/components/BottomNavigation.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'home',
    components: {
      default: HomeView,
      pageFooter: BottomNavigation,
    },
  },
  {
    path: '/',
    name: 'login',
    components: {
      default: () => import('../views/LoginView.vue'),
      // pageFooter: BottomNavigation,
    },
  },
  {
    path: '/list-page',
    name: 'list-page',
    components: {
      default: () => import('../views/ListPage.vue'),
      pageFooter: BottomNavigation,
    },
  },
  {
    path: '/schedule-list',
    name: 'schedule-list',
    components: {
      default: () => import('../views/ScheduleList.vue'),
      pageFooter: BottomNavigation,
    },
  },
  {
    path: '/fav',
    name: 'fav',
    components: {
      default: () => import('../views/FavPage.vue'),
      pageFooter: BottomNavigation,
    },
  },
  {
    path: '/schedule-detail',
    name: 'schedule-detail',
    components: {
      default: () => import('../views/ScheduleDetail.vue'),
      pageFooter: BottomNavigation,
    },
  },
  {
    path: '/user-info',
    name: 'user-info',
    components: {
      default: () => import('../views/UserInfo.vue'),
      pageFooter: BottomNavigation,
    },
  },
  {
    path: '/my-coupon',
    name: 'my-coupon',
    components: {
      default: () => import('../views/MyCoupon.vue'),
      pageFooter: BottomNavigation,
    },
  },
  {
    path: '/notifications-list',
    name: 'notifications-list',
    components: {
      default: () => import('../views/NotificationsList.vue'),
      pageFooter: BottomNavigation,
    },
  },
  {
    path: '/my-page',
    name: 'my-page',
    components: {
      default: () => import('../views/MyPage.vue'),
      pageFooter: BottomNavigation,
    },
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
